import React, { useContext } from 'react';

import {
  Container,
  Description,
  CheckBoxContainer,
  CheckboxDefaultText,
  CheckboxStrongText,
} from './styles';

import { Title } from '../../styles';

/**
 * components
 */
import CustomCheckBox from '../../../../../components/CustomCheckBox';
import { CustomButton } from '../../../../../components/GerenciadorCanais';

/**
 * context
 */
import { AdContext } from '../../adScreen.context';

function Terms() {
  const {
    haveReadTheTerms,
    handleToggleHaveReadTheTerms,
    handleToggleInvitedTheTerms,
  } = useContext(AdContext);

  return (
    <Container>
      <Title>Sobre os anncio(s)</Title>
      <Description>
        Aqui voc pode ativar o mdulo de anncios. E as oportunidades so das
        mais variadas voc pode levar seus ouvintes ao seu site, ao hotsite de
        uma promoo, a um evento que voc queria anunciar,  um patrocinador
        que esteja colaborando com o seu canal!
      </Description>

      <Description>
        S pedimos que seu(s) anncios respeitem nossa poltica de comunidade.
      </Description>

      <CheckBoxContainer>
        <CustomCheckBox
          checked={haveReadTheTerms}
          onChangeCallback={handleToggleHaveReadTheTerms}
        />

        <CheckboxDefaultText>
          Declaro que li a{' '}
          <CheckboxStrongText>poltica de comunidade</CheckboxStrongText> e irei
          segu-la
        </CheckboxDefaultText>
      </CheckBoxContainer>

      <CustomButton
        disabled={!haveReadTheTerms}
        onClick={handleToggleInvitedTheTerms}
      >
        Continuar
      </CustomButton>
    </Container>
  );
}

export default Terms;
