/* eslint-disable no-unused-vars */
import React, { useState } from 'react';

import { Link } from 'react-router-dom';

import {
  ContentTitleForm,
  ForgotPasswordLink,
  FormContainer,
  InputContainer,
  ButtonSingIn,
  ContentText,
} from '../styles';

import {
  TextBadge,
  Badge,
  InputLinksSection,
  InputLinksSectionUnderline,
  Footer,
} from './styles';

function ErroSelecionarCanal({ history, match }) {
  const checkedWhite = require('../../../assets/png/Grupo 2439.png');
  const { push } = history;

  console.log(match);

  const [error, toggleError] = useState(match.params.error);

  return (
    <ContentTitleForm>
      <ForgotPasswordLink
        onClick={() => {
          push('/reivindicacao/buscarcanal');
        }}
      >
        {'<voltar'}
      </ForgotPasswordLink>

      <FormContainer style={{ paddingTop: 86 }}>
        <InputContainer>
          <Badge>
            <img
              style={{ marginRight: 24 }}
              src={checkedWhite}
              alt="checkedWhite.png"
            />
            <TextBadge>No encontramos um e-mail</TextBadge>
          </Badge>

          {handlingText(error)}
        </InputContainer>

        <Footer>
          <ButtonSingIn maxWidth onClick={() => toggleError('2')}>
            Tentar novamente
          </ButtonSingIn>

          {error === '2' && (
            <>
              <InputLinksSection>Ainda no conseguiu?</InputLinksSection>
              <Link to="/reivindicacao/help">
                <InputLinksSectionUnderline>
                  {'Ento entre em contato conosco>'}
                </InputLinksSectionUnderline>
              </Link>
            </>
          )}
        </Footer>
      </FormContainer>
    </ContentTitleForm>
  );

  function handlingText(erro) {
    return (
      <ContentText>
        {erro === '0' // podcast selecionado nao tem email
          ? `Nosso robozinho no encontrou um endereo de email no cdigo do seu
          feed, por favor v no seu feed e insira algum email, s assim conseguimos
          verificar se voc realmente  o dono de "${channelTest.title}, quando fizer
          isso clique em tentar novamente.`
          : erro === '1' // podcast adicionado pela tela do feed mas no possui email
          ? `Seu podcast foi adicionado! Porm, nosso robozinho no encontrou um
          endereo de e-mail! Por favor, insira um e-mail no seu feed, s assim
          conseguiremos confirmar se voc  o dono de "${channelTest.title}.
          Depois que fizer isso, clique em Tentar novamente.`
          : erro === '2' // mesmo apos tentar novamente, o podcaste esta sem email
          ? `Nosso robozinho ainda no encontrou um endereo de email no cdigo
          do seu feed, por favor v no seu feed e insira algum email, s assim
          conseguimos verificar se voc realmente  o dono de "${channelTest.title},
          quando fizer isso clique em tentar novamente.`
          : ' '}
      </ContentText>
    );
  }
}

export default ErroSelecionarCanal;

